import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    count: 0,
    wxback: {}
  },
  mutations: {
    increment: state => state.count++,
    decrement: state => state.count--,
    setwxback(state, data) {
      state.wxback = data;
    }
  }
})


